.phbookThinIcon {
  position: relative;
  width: 57px;
  height: 53px;
  overflow: hidden;
  flex-shrink: 0;
}
.bookE {
  position: relative;
  letter-spacing: 0.04em;
}
.icon {
  width: 653px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: 20px;
}
.homePage {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  font-size: var(--font-size-base);
  font-family: var(--font-secular-one);
  color: var(--color-white);
  text-align: left;
  display: inline-block;
}
.forum,
.homePage {
  position: relative;
  letter-spacing: 0.04em;
  line-height: 150%;
}
.frameIcon {
  position: relative;
  width: 40px;
  height: 40px;
  overflow: hidden;
  flex-shrink: 0;
}
.logOut,
.profile {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
}
.profile {
  width: 104.5px;
  height: 40px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-6xs);
}
.logOut {
  position: relative;
  font-size: var(--font-size-base);
  letter-spacing: 0.04em;
  line-height: 150%;
  font-family: var(--font-secular-one);
  color: var(--color-white);
  text-align: left;
  display: inline-block;
  width: 69px;
  flex-shrink: 0;
}
.vectorIcon {
  position: relative;
  width: 16px;
  height: 20px;
}
.button,
.logout,
.navbar {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.logout {
  width: 88.38px;
  height: 24px;
  justify-content: flex-start;
  gap: var(--gap-10xs);
}
.button,
.navbar {
  width: 613px;
  justify-content: space-between;
  font-size: var(--font-size-base);
  font-family: var(--font-secular-one);
}
.navbar {
  margin: 0;
  position: absolute;
  top: 5px;
  left: 35px;
  width: 1280px;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--color-white);
  font-family: var(--font-amita);
}
.blurBgEdIcon {
  position: absolute;
  top: 59px;
  left: 0;
  width: 1355px;
  height: 563px;
  overflow: hidden;
}
.userProfilePicIcon {
  position: absolute;
  top: 157px;
  left: 841px;
  border-radius: var(--br-12xl);
  width: 355px;
  height: 355px;
  object-fit: cover;
}
.name {
  margin: 0;
  position: absolute;
  top: 82px;
  left: 89px;
  font-size: inherit;
  font-weight: 400;
  font-family: inherit;
  display: flex;
  align-items: center;
  width: 1055px;
  height: 96px;
}
.photoUpload {
  position: absolute;
    font-size: small;
    width: 200px;
    height: 35px;
    top: 300px;
    left: 594px;
    max-width: 100%;
    color: #0b671d;
    padding: 5px;
    background: none;
    border-radius: 10px;
    border: 1px solid #555;
}
.doneEditing {
  position: relative;
  font-size: var(--font-size-smi);
  letter-spacing: 0.04em;
  line-height: 150%;
  font-weight: 600;
  font-family: var(--font-inter);
  color: var(--color-gray-300);
  text-align: left;
}
.editDoneButton,
.frame {
  width: 156px;
  display: flex;
  cursor: pointer;
}
.editDoneButton {
  border: 0;
  padding: var(--padding-3xl) var(--padding-11xl);
  background-color: var(--color-skyblue);
  border-radius: var(--br-smi);
  height: 45px;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
}
.frame {
  position: absolute;
  top: 343px;
  left: 594px;
  height: 58px;
  overflow: hidden;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.frameParent {
  position: absolute;
  top: 197px;
  left: 76px;
  width: 896px;
  height: 420px;
}
.editProfile {
  position: relative;
  background-color: var(--color-gray-400);
  width: 100%;
  height: 635px;
  overflow: auto;
  text-align: left;
  font-size: var(--font-size-26xl);
  color: var(--color-powderblue);
  font-family: var(--font-secular-one);
}
